html {
  font-family: Roboto, sans-serif;
}

body {
  --blue: #00acdc;
  margin: 0;
  display: flex;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.header-content {
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_list-item {
  margin-right: 20px;
  display: inline-block;
}

li:last-child {
  margin: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav_link:hover, .is-active {
  color: var(--blue);
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

